@import "../../../helpers/styles.scss";

.mp-instruction-layout {
    display: flex;
    flex-direction: column;
    height: calc(100vh - 98px);
    width: auto;
    gap: 1rem;

    --primary-color: #fa7474;
    --secondary-color: #ffd6b0;
    --tertiary-color: #e9efc5;
    --text-color-1: #333333;

    /* Resets */
    h6,
    p {
        margin: 0;
        padding: 0;
    }
    .mp-instruction-header {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 2rem;
    }
    /* Body */
    .instruction-layout-body {
        height: calc(100% - 104px);
    }

    .collection-input-container {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.25rem;

        input {
            width: 3rem;
            aspect-ratio: 1;
            text-align: center;
            border: 1px solid #3e3e3e;
            border-radius: 8px;
            font-size: 1.125rem;
        }
        button {
            /* reset styles */
            background-color: transparent;
            border: none;
            outline: none;
            cursor: pointer;

            width: 1.5rem;
            aspect-ratio: 1;
            padding: 0.25rem;
            border-radius: 8px;

            font-size: 30px;

            &:active {
                background-color: #3e3e3e44;
                transform: scale(0.9);
            }
            &:hover {
                background-color: #3e3e3e44;
            }
        }
    }
}
/* Footer */
.material-planner-layout-footer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 0 2rem;
}
