@import "../../../helpers/styles.scss";

.material-planner-layout {
    display: flex;
    flex-direction: column;
    height: calc(100vh - 98px);
    width: auto;
    gap: 1rem;

    --primary-color: #fa7474;
    --secondary-color: #ffd6b0;
    --tertiary-color: #e9efc5;
    --text-color-1: #333333;

    /* Resets */
    h6,
    p {
        margin: 0;
        padding: 0;
    }

    .material-planner-layout-header {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 2rem;

        h4 {
            margin-right: auto;
        }
        .overview-metrics-container {
            display: flex;
            gap: 2rem;
        }
        .overview-metrics-box {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 1rem;
            width: 12rem;
            min-width: 10rem;
            aspect-ratio: 3/1;

            border-radius: 8px;

            p {
                margin: 0;
                padding: 0;
                font-size: 1.25rem;
                color: var(--text-color-1);
            }

            p:last-child {
                font-size: 1.875rem;
                font-weight: 600;
            }
        }
        .overview-last-refresh {
            display: flex;
            align-items: flex-end;
            flex-direction: column;
            gap: 0.25rem;
        }
    }

    /* Body */
    .material-planner-layout-body {
        height: 100%;
    }
}

.orderDetailsContainer {
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 2.76vw;
    margin-left: 0;
    margin-bottom: 2.76vw;
}
.leftPart,
.rightPart {
    width: 30%;
    display: flex;
    flex-direction: column;
}

.rightPart {
    width: 70%;
}

.orderDetailKeyList,
.orderDetailValueList {
    list-style: none;
    font-size: 1.04vw;
    line-height: 3.5vw;
    color: black;
    font-weight: 500;
}

.orderDetailValueList {
    font-weight: 700;
}


/* Action Modal Styles */
.action_modal__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 2rem;
}
